.PropertiesDrawerContents-sliderContainer {
  margin-top: 8px !important; }

.PropertiesDrawerContents-sliderValue {
  display: block;
  float: right; }

.PropertiesDrawerContents-spreadsheetIcon {
  margin-right: -6px !important; }

.PropertiesDrawerContents-buttonListItem {
  padding-top: 4px !important; }

.PropertiesDrawerContents-buttonListItemText {
  padding-right: 0 !important; }



