.Viewport {
  background-color: white;
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%; }

.Viewport-contents {
  position: relative;
  top: 0;
  left: 0;
  -webkit-transform-origin: 0 0 0;
      -ms-transform-origin: 0 0 0;
          transform-origin: 0 0 0; }



