/* @import "@material/typography/mdc-typography.scss"; */
.NodeView {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: visible; }

.NodeView-content {
  font-weight: bold;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 150px;
  position: absolute;
  background-color: #ef6c00;
  color: white;
  border: 1px solid white;
  padding: 7px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  word-break: break-word; }
  .NodeView-content.shape-oval {
    border-radius: 50%; }
  .NodeView-content.shape-pill {
    border-radius: 1000px; }
  .NodeView-content a {
    color: white;
    text-decoration: none; }
    .NodeView-content a:hover {
      text-decoration: underline; }
  .NodeView-content h1, .NodeView-content h2, .NodeView-content h3, .NodeView-content h4, .NodeView-content h5, .NodeView-content h6 {
    margin: 0; }
  .NodeView-content img {
    display: block;
    max-width: 150px !important; }

.NodeView-content.locked {
  border: 3px solid rgba(0, 0, 0, 0.25);
  outline: 2px solid rgba(0, 0, 0, 0.5);
  outline-offset: 1px; }

.NodeView-content.selected {
  color: white;
  background-color: #43a047 !important;
  border: 3px solid rgba(0, 0, 0, 0.25) !important;
  z-index: 100;
  -webkit-box-shadow: 0px 0px 25px 5px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 25px 5px rgba(0, 0, 0, 0.25); }



